@media only screen and (max-width: 600px) {
  h1, h2, h3, h4, h5, h6 {
   font-size: 5vw;
  }
  img {
    height: 600px;
  }
  .container-fluid {
    margin-bottom: 10%;
  }

}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

p, h1, h2, h3, h4, h5, h6 {
 color: Crimson;
}

.full {
  max-width: 100%;
  max-height: 100%;
}

.title {
  max-width: 100%;
  max-height: 100%;
}

.col {
  text-align: center;
}

img {
  box-shadow: 10px 9px 5px grey;
}



#title {
  position: relative;
}

#titleStuff {
  left: 10%;
}

#titleContent{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.container-fluid {
  padding-bottom: 5%;
}
.carousel-image{
  max-height: 500px;
}
